<template>
  <div>
    <div v-once class="header">
      <div class="container container-header">
        <div class="logo">
          <img :src="this.publicPath + 'footer.png'" alt="" width="140px" height="auto" />
        </div>
        <div class="header-items">
          <p class="header-item">
            <img class="header-icon" :src="this.publicPath + 'pin.svg'" alt="" width="20px" height="20px" />
            <span class="header-extra-info"> : </span>
            , 83 |  "", 3 
          </p>
          <p class="header-item">
            <img class="header-icon" :src="this.publicPath + 'time.svg'" alt="" width="20px" height="20px" />
            <span class="header-extra-info"> :  </span>
             10:00  21:00
          </p>
        </div>
        <div class="header-info">
          <a class="link header-tel" href="tel: +79000939392" title=" ">+7 (900) 093-93-92</a>
          <a class="link header-vk" href="https://vk.com/mgnvr" target="_blank" title=" ">
          </a>
        </div>
      </div>
    </div>
    <div class="container container-contactus">
      <div class="breadcrumb-container">
        <router-link class="link-home" tag="a" to="/" title="  ">
            
        </router-link>
      </div>
      <h1 class="about-header"> </h1>
      <form action="https://send.pageclip.co/YR97n0GUKhRoDRB8QgxgjFgUXX86SOa5" method="post"
        class="form-reserve pageclip-form" autocomplete="off">
        <input type="text" name="" value="" placeholder="" class="input-reserve" title="" required>
        <input type="tel" class="input-reserve" id="tel" name="" placeholder="   8-(XXX)-XXX-XX-XX"
          pattern="8-\([0-9]{3}\)-[0-9]{3}-[0-9]{2}-[0-9]{2}" title="" required>
        <div class="datetime-reserve">
          <input type="text" id="date" name="" class="input-reserve" placeholder="   ."
            pattern="\d{2}.\d{2}" title="" required>
          <input type="text" id="time" name="" class="input-reserve" placeholder="   :" title=""
            pattern="\d{2}:\d{2}" required>
        </div>
        <input type="number" id="count" name=" " class="input-reserve"
          placeholder="  ( - 5)" min="1" max="5" title="" required>
        <textarea name="" rows="5" placeholder=" "
          class="input-reserve textarea-reserve" title=""></textarea>
        <button class="button-reserve button pageclip-form__submit" type="submit">
          <span></span>
        </button>
      </form>
    </div>
    <div class="footer">
      <div class="container container-footer">
        <div class="logo">
          <router-link class="about-link" tag="a" to="/about" title=" ">
            <img :src="this.publicPath + 'footer.png'" width="200px" height="auto" alt="DriV3R">
          </router-link>
        </div>
        <div class="tel">
          <a href="tel: +79000939392" title=" ">+7 (900) 093-93-92</a>
        </div>
        <div class="social">
          <a href="https://vk.com/mgnvr" target="_blank" title=" ">
            <img :src="this.publicPath + 'vk.svg'" alt="" width="30px" height="30px">
          </a>
        </div>
        <div class="copyright">
          &#9400; Driv3r, 2023 |          
        </div>
        <div class="developer">
             <a href="https://vk.com/primirenkov" target="_blank"><img :src="this.publicPath + 'ps.svg'"
              alt=" "></a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import IMask from 'imask'

export default {
  name: 'contactus',
  data() {
    return {
      sitename: 'Driv3r -  ',
      publicPath: process.env.BASE_URL
    }
  },
  mounted() {
    function setCaretPosition(elemId, caretPos) {
      var elem = document.getElementById(elemId);

      if (elem != null) {
        if (elem.createTextRange) {
          var range = elem.createTextRange();
          range.move('character', caretPos);
          range.select();
        } else {
          if (elem.selectionStart) {
            elem.focus();
            elem.setSelectionRange(caretPos, caretPos);
          } else
            elem.focus();
        }
      }
    }

    let showhideTel = document.getElementById('tel');
    let patternMaskTel = new IMask(showhideTel, {
      mask: '{8}-(000)-000-00-00',
      greedy: false,
      lazy: true,
      placeholderChar: '_'

    });
    showhideTel.addEventListener('focus', function () {
      patternMaskTel.updateOptions({ lazy: false });
      setCaretPosition('tel', 3);
    }, true);
    showhideTel.addEventListener('blur', function () {
      patternMaskTel.updateOptions({ lazy: true });
      if (!patternMaskTel.masked.rawInputValue) {
        patternMaskTel.value = '';
      }
    }, true);

    let showhideDate = document.getElementById('date');
    let patternMaskDate = new IMask(showhideDate, {
      mask: '00.00',
      lazy: true,
      placeholderChar: '_'
    });
    showhideDate.addEventListener('focus', function () {
      patternMaskDate.updateOptions({ lazy: false });
      setCaretPosition('date', 0);
    }, true);
    showhideDate.addEventListener('blur', function () {
      patternMaskDate.updateOptions({ lazy: true });
      if (!patternMaskDate.masked.rawInputValue) {
        patternMaskDate.value = '';
      }
    }, true);

    let showhideTime = document.getElementById('time');
    let patternMaskTime = new IMask(showhideTime, {
      mask: '00:00',
      lazy: true,
      placeholderChar: '_'
    });
    showhideTime.addEventListener('focus', function () {
      patternMaskTime.updateOptions({ lazy: false });
      setCaretPosition('time', 0);
    }, true);
    showhideTime.addEventListener('blur', function () {
      patternMaskTime.updateOptions({ lazy: true });
      if (!patternMaskTime.masked.rawInputValue) {
        patternMaskTime.value = '';
      }
    }, true);

    let form = document.querySelector('.pageclip-form')
    Pageclip.form(form, {
      onSubmit: function (event) { },
      onResponse: function (error, response) { },
      successTemplate: "<span>,   </span><a class='back-home' href='https://mgnvr.github.io/driv3r/#/'>  </a><button class='form-close'></button>"
    })
  }
}

</script>
